	  function createTreeViewDataSource(objectTypeName, objects) {
	    var layerName = ROAM_LAYERS['ROAM_' + objectTypeName.toUpperCase()];
	    var data = [];
	    var root = {
	      layerName: layerName,
	      root: true,
	      id: objectTypeName.toLowerCase(),
	      text: _app2.default.translate(objectTypeName),
	      items: [],
	      objectIds: [],
	      expanded: true
	    };
	
	    var configurationFilterEnabled = viewer.testIsAssetConfigurationFilterEnabled(layerName);
	    var configurations = {};
	    var categories = {};
	
	    _.forEach(objects, function (o) {
	
	      /*
	       * Leaf nodes
	       */
	      var domainObjectTypeId = o.domainObjectTypeId;
	      var asset = o.asset;
	      var name = o.name;
	
	      var leaf = {
	        layerName: layerName,
	        leaf: true,
	        id: domainObjectTypeId,
	        text: asset ? asset.name : name,
	        objectIds: [domainObjectTypeId]
	      };
	
	      if (layerName === ROAM_ASSETS_HISTORY) {
	        leaf.text = asset.name + ' ' + name;
	      }
	
	      /*
	       * Category nodes
	       */
	      var locationType = o.locationType;
	      var fenceType = o.fenceType;
	      var assetCategory = o.assetCategory;
	
	      var categoryName = locationType || fenceType || assetCategory || 'Unknown';
	      var category = categories[categoryName];
	
	      if (!category) {
	        category = categories[categoryName] = {};
	
	        _.assign(category, {
	          layerName: layerName,
	          category: true,
	          name: _app2.default.translate(categoryName),
	          id: categoryName.toLowerCase(),
	          imageUrl: styler.getObjectCategoryIcon(o),
	          items: [],
	          objectIds: []
	        });
	      }
	
	      var categoryItems = category.items;
	
	      categoryItems.push(leaf);
	      category.objectIds.push(leaf.id);
	      category.text = category.name + ' (' + categoryItems.length + ')';
	
	      /*
	       * Configuration nodes
	       */
	      if (configurationFilterEnabled) {
	        var configName = o.configurationName || 'Unknown';
	        var config = configurations[configName];
	
	        if (!config) {
	          config = configurations[configName] = {};
	
	          _.assign(config, {
	            layerName: layerName,
	            configuration: true,
	            name: _app2.default.translate(configName),
	            items: [],
	            objectIds: []
	          });
	        }
	
	        var configItems = config.items;
	
	        if (!_.find(configItems, { id: category.id })) {
	          configItems.push(category);
	          config.text = config.name + ' (' + configItems.length + ')';
	        }
	
	        config.objectIds.push(leaf.id);
	      }
	    });
	
	    var rootItems = !_.isEmpty(configurations) ? configurations : categories;
	
	    root.items = _(rootItems).map().compact().forEach(function (rootItem) {
	      root.objectIds = [].concat(_toConsumableArray(root.objectIds), _toConsumableArray(rootItem.objectIds));
	    });
	
	    data.push(root);
	
	    return data;
	  }
	
